import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd, Event } from '@angular/router';
import {HeaderInfo, Menus} from '../app.config';
import {IbukiService} from '../services/ibuki.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  subs : any;
  headerData: any;
  menus : any;
  selectedMenu: any = "home";

  constructor(private router: Router,private ibuki: IbukiService) { 
    this.headerData = HeaderInfo;
    this.menus = Menus;
  }

  ngOnInit() {
    this.subs = this.ibuki.filterOn('selectedMenu').subscribe(d => {
      if (d.error) {
        console.log(d.error);
      } else {
        this.selectedMenu = d.data
      }
    });
  }

  navigate(menu)
  {
    this.selectedMenu = menu;
  }
  
  ngOnDestroy() {
    this.subs.unsubscribe();
  }
}
