import { Component, OnInit } from '@angular/core';
import {Home} from '../app.config'
import {IbukiService} from '../services/ibuki.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  content : any;
  headerImage : any;
  constructor(private ibuki: IbukiService) { }

  ngOnInit() {
    this.ibuki.emit("selectedMenu", "home");
    this.content = Home.content;
    this.headerImage = Home.headerImage;
  }

  ngOnDestroy() {

  }

}
